.profileContainer {
  /* background-color: red; */
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  min-height: -webkit-min-content;
  min-height: -moz-min-content;
  min-height: min-content;
  height: 100%;
  /* min-height: 1000px !important; */
}
.profileHeader {
  /* background-color: blue; */
  display: -ms-flexbox;
  display: flex;
  width: 70%;
  -ms-flex-preferred-size: 20%;
      flex-basis: 20%;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  /* padding-bottom: 1%; */
}
.profileBody {
  /* background-color: yellow; */
  width: 70%;
  padding-top: 2%;
  -ms-flex-preferred-size: 50%;
      flex-basis: 50%;
  display: -ms-flexbox;
  display: flex;
  border-top: 1px solid #D1CDCD;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}
.profileFooter {
  padding-top: 3%;
  min-width: 100%;
  -ms-flex-preferred-size: 30%;
      flex-basis: 30%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}
@media only screen and (max-width: 767px) {
  .profileContainer {
    min-width: 300px;
  }
  .profileHeader, .profileBody, .profileFooter {
    width: 90%;
  }
  .profileHeader {
    height: 300px;
  }
  .profileBody {
    height: 550px;
  }
  .profileContainer {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
  } 
}
@media only screen and (min-width: 768px) and (max-width: 1200px){
  .profileContainer {
    min-height: 750px;
  } 
  .profileHeader, .profileBody, .profileFooter {
    width: 80%;
  }
}
@media only screen and (min-width: 1200px){
  .profileContainer {
    min-height: 850px;
  } 
  .profileFooter {
    padding-bottom: 3%;
  }
}


