.profileCloseButton{
  -ms-flex-preferred-size: 5%;
      flex-basis: 5%;
  color: #A09E9E;
  position: relative;
  left: 65%;
}
.profileAvatar {
  /* background-color: lightblue; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-preferred-size: 40%;
      flex-basis: 40%;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}
.avatar_title_name {
  color: rgb(2, 102, 182);
  font-size: 20px;
  font-weight: bold;
}
.profileActivity {
  /* background-color: lightskyblue; */
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-preferred-size:20%;
      flex-basis:20%;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
}
.profileActivityItem {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-preferred-size: fit-content;
      flex-basis: fit-content;
}
.profileActivityItemName:after {
  content: "\00a0";
}
.profileActivityItemValue {
  font-size: 0.8rem;
}
@media only screen and (max-width: 767px) {
  .profileAvatar {
    -ms-flex-preferred-size: 30%;
        flex-basis: 30%;
  }
  .profileActivity {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-preferred-size: 40%;
        flex-basis: 40%;
    -ms-flex-positive: 1;
        flex-grow: 1;
  }
  .profileActivityItem {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
  }
  .profileActivityItemName {
    padding-bottom: 1%;
  }
  .avatar_title_name {
    font-size: 17px;
    padding-bottom: 15%;
  }
  .profileCloseButton{
    left: 40vw;
  }
}

@media only screen and (min-width: 768px) and (max-width: 1200px){
  .profileCloseButton{
    left: 45vw;
  }
}

