.sticky {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  position: relative;
  height: 35px;
  top: -15px;
}
.sticky > button {
  font-weight: bold;
  background-color: white;
  border: 1px solid rgb(229, 229, 229);
}

.questionContainer .sticky button:hover {
  background-color: rgb(233, 243, 253);
  -webkit-box-shadow: 0 0 2px rgb(2, 102, 182);
          box-shadow: 0 0 2px rgb(2, 102, 182);
}

.answerContainer .sticky button:hover {
  background-color: rgb(243, 243, 246);
  -webkit-box-shadow: 0 0 2px rgb(100, 105, 111);
          box-shadow: 0 0 2px rgb(100, 105, 111);
}


