.avatar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
}
.avatar:not(.avatar-self)>img:hover {
  cursor: pointer;
  border: 2px solid rgb(2, 102, 182);
  -webkit-box-shadow: 0 0 5px rgb(73, 154, 214);
          box-shadow: 0 0 5px rgb(73, 154, 214);
  border-radius: 50%;
}

.avatar-big>img {
  width: 70px;
  height: 70px;
  -o-object-fit: cover;
     object-fit: cover;
  border-radius: 50%;
}
.avatar-medium>img {
  width: 50px;
  height: 50px;
  -o-object-fit: cover;
     object-fit: cover;
  border-radius: 50%;
}


