.item {
  display: -ms-flexbox;
  display: flex;
  background-color: lightyellow;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-preferred-size: 15%;
      flex-basis: 15%;
  height: 80%;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}
.item .avatar, .item>.more_activities_count {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-preferred-size: 70%;
      flex-basis: 70%;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.item>.item_status {
  font-size: calc(11em / 16);
  -ms-flex-preferred-size: 22%;
      flex-basis: 22%;
  width: 80%;
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  color: rgb(167, 169, 172);
  border-top: 1px solid rgb(167, 169, 172);
}
.item.item_asked {
  height: 100%;
  -ms-flex-pack: end;
      justify-content: flex-end;
  background-color: rgb(233, 243, 253);
}
.item.item_asked>.item_status {
  max-height: calc(0.80 * 22%);
  background-color: rgb(233, 243, 253);
}
.item.more_activities>.item_status {
  -ms-flex-positive: 1;
      flex-grow: 1;
  border: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.item .item_status_circle {
  position: relative;
  top: 5px;
  background: rgb(167, 169, 172);
  width: 10px;
  height: 10px;
  border-radius: 50%;
}
.item.answered>.item_status {
  border-top: 2px solid rgb(75, 74, 76);
}
.item.answered .item_status_circle {
  background: rgb(75, 74, 76);
}
.item.item_asked .item_status_circle{
  visibility: hidden;
}
.more_activities {
  background-color: white;
}
.commented, .answered {
  background-color: rgb(238, 238, 238);
}
@media only screen and (max-width: 700px){
  .item.commented{
    display: none;
  }
  .item {
    -ms-flex-preferred-size: 30%;
        flex-basis: 30%;
  }
}
@media only screen and (max-width: 400px){
  .item.item_asked{
    display: none;
  }
  .item {
    -ms-flex-preferred-size: 49%;
        flex-basis: 49%;
    height: 99%;
  }
  
  .question_details_content {
    -ms-flex-preferred-size: 50%;
        flex-basis: 50%;
  }
  .question_details_popularity {
    -ms-flex-positive: 50;
        flex-grow: 50;
    padding-left: 6%;
    -ms-flex-item-align: center;
        align-self: center;;
  }
  .more_activities {
    background-color: rgb(238, 238, 238);
  }
}


