/* How it started */
.navigationTable { 
  /* background-color: lightyellow; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-preferred-size: 10%;
      flex-basis: 10%;
  width: 100%;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}
.navigationTable p{
  font-style: italic;
  font-weight: bold;
  font-size: calc(42rem / 16);
  /* font-size: 42px; */
}
.profileStatistics {
  /* background-color: greenyellow; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 0 45%;
      flex: 1 0 45%;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
}
.profileStatisticsData {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 70%;
  max-width: 500px;
}
.profileStatisticsData .square {
  background-color: rgba(229,245,253, 0.8);
  width: 90px;
  height: 90px;
}
.peers {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}

.discussions {
  -webkit-transform: rotate(-15deg);
      -ms-transform: rotate(-15deg);
          transform: rotate(-15deg);
}
.findings {
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}
.questions {
  -webkit-transform: rotate(5deg);
      -ms-transform: rotate(5deg);
          transform: rotate(5deg);
}
.peers_text, .findings_text{
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
  -webkit-transform-origin: 80% 80%;
      -ms-transform-origin: 80% 80%;
          transform-origin: 80% 80%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
}
.discussions_text {
  -webkit-transform: rotate(15deg);
      -ms-transform: rotate(15deg);
          transform: rotate(15deg);
  -webkit-transform-origin: -40% 75%;
      -ms-transform-origin: -40% 75%;
          transform-origin: -40% 75%;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.questions_text {
  -webkit-transform: rotate(-4deg);
      -ms-transform: rotate(-4deg);
          transform: rotate(-4deg);
  -webkit-transform-origin: 350% 100%;
      -ms-transform-origin: 350% 100%;
          transform-origin: 350% 100%;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
/* People who joined the platform */
.newPeople {
  /* background-color: #F9F91D; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-preferred-size: 40%;
      flex-basis: 40%;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding-bottom: 1%;
}
.newPeople .people {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-preferred-size: 60%;
      flex-basis: 60%;
  -ms-flex-positive: 1;
      flex-grow: 1;
  padding-top: 6%;
  /* padding-bottom: 1%; */
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.newPeople .people .person {
  -ms-flex-preferred-size: 30%;
      flex-basis: 30%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  /* background-color: magenta; */
}
.personAvatar {
  background-size: 6px 6px;
  background-image:-webkit-linear-gradient(45deg, transparent 46%, #E5D4D4 49%, rgb(240, 240, 240) 51%, transparent 55%);
  background-image:-o-linear-gradient(45deg, transparent 46%, #E5D4D4 49%, rgb(240, 240, 240) 51%, transparent 55%);
  background-image:linear-gradient(45deg, transparent 46%, #E5D4D4 49%, rgb(240, 240, 240) 51%, transparent 55%);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  -ms-flex-preferred-size: 70%;
      flex-basis: 70%;
}
@media only screen and (max-width: 767px) {
  .navigationTable p {
    font-size: 25px;
  }
  .profileStatisticsData {
    -ms-flex-pack: center;
        justify-content: center;
    width: 200px;
  }
  .newPeople {
    padding-bottom: 8%;
  }
}

